/* 动画库 */
/* 向下 */
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-1rem);
  }
  60% {
    transform: translateY(-0.5rem);
  }
}
@keyframes swing {
  to {
    transform: rotateY(6deg) skewY(6deg);
    transform: translate(10px, -10px);
  }
}
