@import url(../animation.css);
:root {
  --mainColor: orange;
  --tabNavColor: transparent;
}
.tabNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}
.tabNav .title {
  position: relative;
  z-index: -1;
}
.tabNav .title .top {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}
.tabNav .title .top::after {
  content: '';
  height: 6px;
  width: 100%;
  background-color: orange;
  position: absolute;
  bottom: -8px;
}
.tabNav .title .top > div:nth-of-type(1) {
  width: 40px;
  height: 30px;
  margin-right: 20px;
  background-color: orange;
}
.tabNav .title .top > div:nth-of-type(2) {
  font-size: 30px;
  font-weight: 700;
  color: white;
}
.tabNav .title .bottom {
  font-size: 25px;
  margin-top: 10px;
  margin-bottom: 15px;
  font-weight: 100;
  color: #FFFFCC;
}
.tabNav .meun_btn {
  position: relative;
  z-index: 99;
}
.tabNav .meun_btn {
  width: 90px;
  height: 60px;
  margin: 10px 10px;
  position: relative;
  cursor: pointer;
  display: inline-block;
}
.tabNav .meun_btn span {
  background-color: orange;
  position: absolute;
  border-radius: 2px;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 6px;
}
.tabNav .meun_btn span:nth-child(1) {
  top: 0px;
  left: 0px;
}
.tabNav .meun_btn span:nth-child(2) {
  top: 27px;
  left: 0px;
}
.tabNav .meun_btn span:nth-child(3) {
  bottom: 0px;
  left: 0px;
}
.tabNav .meun_btn.active span:nth-child(1) {
  transform: rotate(45deg);
  top: 27px;
  background-color: white;
}
.tabNav .meun_btn.active span:nth-child(2) {
  opacity: 0;
}
.tabNav .meun_btn.active span:nth-child(3) {
  transform: rotate(-45deg);
  top: 27px;
  background-color: white;
}
.meunList {
  position: fixed;
  z-index: 90;
  right: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  width: 100vw;
  background: orange;
  overflow: hidden;
  border-bottom-left-radius: 200%;
  -webkit-transition: -webkit-transform cubic-bezier(0.77, 0, 0.175, 1) 0.6s, border-radius linear 0.8s;
  transition: -webkit-transform cubic-bezier(0.77, 0, 0.175, 1) 0.6s, border-radius linear 0.8s;
  transition: transform cubic-bezier(0.77, 0, 0.175, 1) 0.6s, border-radius linear 0.8s;
  transition: transform cubic-bezier(0.77, 0, 0.175, 1) 0.6s, -webkit-transform cubic-bezier(0.77, 0, 0.175, 1) 0.6s, border-radius linear 0.8s;
  -webkit-transform: translateX(100%) translateY(-100%);
  transform: translateX(100%) translateY(-100%);
}
.meunList .content {
  box-sizing: border-box;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100%;
  width: 100%;
  justify-content: center;
  background: url(../../static/back.png);
  background-repeat: no-repeat;
  background-size: cover;
}
.meunList .content .item {
  font-size: 40px;
  position: relative;
  font-family: "微软雅黑";
  color: rgba(0, 0, 0, 0.651);
  padding: 5% 30px;
  display: inline-block;
  font-weight: 900;
}
.meunList .content .active {
  color: red;
  position: relative;
}
.meunList .content .active::after {
  content: '';
  width: calc(100%);
  position: absolute;
  left: 0;
  top: calc(50% - 5px);
  height: 10px;
  border-radius: 5px;
  background-color: #0099ccb2;
  animation: toheight 0.3s linear;
}
@keyframes toheight {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.nav_active {
  -webkit-transform: translateX(0%) translateY(0%);
  transform: translateX(0%) translateY(0%);
  border-radius: 0;
}
.nav_active {
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
.nav_active {
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}
.bounce {
  animation: bounce 4s 2s infinite;
  position: absolute;
  z-index: 99;
  left: 0;
  bottom: 0;
  top: 0;
  right: 0;
  margin: auto;
  width: 60px;
  height: 60px;
  top: 90%;
}
.readme {
  height: 500px;
  position: relative;
}
.readme .back {
  background-color: #0099cc;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  left: 0;
  top: 0;
}
.readme .back .zheBack {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 153, 204, 0.548);
}
.readme .back .zheDesc {
  position: absolute;
  right: 10%;
  top: 50%;
  font-size: 20px;
  color: #FFFFCC;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
}
.readme .back .zheDesc > div:nth-of-type(1) {
  font-weight: 600;
}
.readme .back .zheDesc > div:nth-of-type(2) {
  font-weight: 100;
  font-size: 28px;
  margin-top: 10px;
}
.readme .back .img {
  width: 61%;
  height: 100%;
  object-fit: cover;
}
.readme .mycard {
  position: absolute;
  bottom: 0;
}
.pc {
  display: none;
  width: 100%;
  height: 100%;
  background-color: var(--mainColor);
  padding: 10%;
  padding-right: 30%;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
}
.pc .radiol {
  position: absolute;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: radial-gradient(circle, orange 20%, #c8ff00 80%, orange 100%);
}
.pc .radiol1 {
  left: -30px;
  top: -30px;
}
.pc .radiol2 {
  animation: swing 1s cubic-bezier(0.42, 0, 0.58, 1) alternate infinite;
  width: 100%;
  height: 100%;
  right: -50%;
  top: 50%;
}
.pc .radiol3 {
  animation: swing 2s cubic-bezier(0.42, 0, 0.58, 1) alternate infinite;
  width: 240px;
  height: 240px;
  left: 100px;
  top: 50%;
}
.pc .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.pc .top .left {
  display: flex;
  align-items: center;
}
.pc .top .left img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: block;
}
.pc .top .left div {
  font-size: 30px;
  margin-left: 10px;
  font-weight: 900;
  color: rgba(255, 255, 255, 0.781);
}
.pc .top .right {
  font-size: 30px;
  font-weight: 900;
  background: linear-gradient(to right, gold, orange);
  -webkit-background-clip: text;
  color: transparent;
}
.pc .middle {
  position: relative;
  width: 100%;
  font-size: 36px;
  font-family: Raleway, Verdana, Arial;
  color: #fff;
}
.pc .white {
  position: absolute;
  left: -10px;
  width: 100%;
  height: 3px;
  background: var(--mainColor);
  z-index: 4;
  animation: whiteMove 3s ease-out infinite;
}
.pc .middle::before {
  content: attr(data-word);
  position: absolute;
  top: 0;
  left: 0.5px;
  height: 0px;
  color: rgba(255, 255, 255, 0.9);
  overflow: hidden;
  z-index: 2;
  animation: redShadow 1s ease-in infinite;
  filter: contrast(200%);
  text-shadow: 1px 0 0 red;
}
.pc .middle::after {
  content: attr(data-word);
  position: absolute;
  top: 0;
  left: -3px;
  height: 36px;
  color: rgba(255, 255, 255, 0.8);
  overflow: hidden;
  z-index: 3;
  background: var(--mainColor);
  animation: redHeight 1.5s ease-out infinite;
  filter: contrast(200%);
  text-shadow: -1px 0 0 cyan;
  mix-blend-mode: darken;
}
@keyframes redShadow {
  20% {
    height: 32px;
  }
  60% {
    height: 6px;
  }
  100% {
    height: 42px;
  }
}
@keyframes redHeight {
  20% {
    height: 42px;
  }
  35% {
    height: 12px;
  }
  50% {
    height: 40px;
  }
  60% {
    height: 20px;
  }
  70% {
    height: 34px;
  }
  80% {
    height: 22px;
  }
  100% {
    height: 0px;
  }
}
@keyframes whiteMove {
  8% {
    top: 38px;
  }
  14% {
    top: 8px;
  }
  20% {
    top: 42px;
  }
  32% {
    top: 2px;
  }
  99% {
    top: 30px;
  }
}
.pc .bottom {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
}
.pc .bottom div {
  font-size: 16px;
  color: white;
}
.pc .bottom > div:last-child {
  background: linear-gradient(to right, white, orange);
  -webkit-background-clip: text;
  color: transparent;
}
@media screen and (min-width: 640px) {
  .tabNav {
    display: none;
  }
  .phone {
    display: none;
  }
  .pc {
    display: block;
  }
  .meunList {
    display: none;
  }
  .showContent {
    float: right;
    right: 0;
    top: 0;
    overflow: auto;
    bottom: 0;
  }
}
@media screen and (min-width: 1000px) {
  html {
    font-size: 6.25rem !important;
  }
  .readme {
    height: 100vh !important;
  }
}
.aboutme {
  position: relative;
}
.aboutme .myhead {
  width: 100%;
  height: 450px;
  position: relative;
  padding: 30px;
  box-sizing: border-box;
}
.aboutme .myhead img {
  position: absolute;
  width: 75%;
  object-fit: cover;
  object-position: 0 0;
  height: 80%;
}
.aboutme .myhead .back {
  width: 75%;
  height: 80%;
  box-sizing: border-box;
  border: 15px solid #FF9900;
  position: absolute;
  bottom: 0;
  right: 5%;
}
.aboutme .content {
  font-size: 30px;
  color: #999966;
}
.aboutme .content > div:nth-of-type(1) {
  font-size: 30px;
  color: black;
  font-weight: 600;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 10px;
}
.aboutme .content > div:nth-of-type(2) {
  font-size: 25px;
  padding: 0 30px;
}
.myprojects {
  position: relative;
}
.myprojects .content {
  margin: 30px;
}
.myprojects .content img {
  width: 33.3%;
  padding: 10px;
  box-sizing: border-box;
  object-fit: contain;
}
.logo {
  margin-top: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.logo .top {
  font-size: 16px;
  font-weight: 700;
}
.logo .bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.logo .bottom > span:nth-of-type(1),
.logo .bottom > span:nth-of-type(3) {
  display: block;
  height: 10px;
  width: 20%;
  background-color: #FF9900;
}
.logo .bottom > span:nth-of-type(2) {
  margin: 0 30px;
  font-size: 16px;
}
