*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



.timeline ul li {
  list-style-type: none;
  position: relative;
  width: 6px;
  margin: 0 auto;
  padding-top: 50px;
  background: #f5af19;
}
.timeline ul li::after {
  content: '';

  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%) rotate(45deg);
  width: 20px;
  border-radius: 50%;
  height: 20px;
  z-index: 2;
  border: 5px solid white;
  background: #f5af19;
}
.lineContent {
  position: relative;
  bottom: 0;
  width: 200px;
  padding: 20px;
  background: #fff;
  box-shadow: 4px 13px 30px 1px rgba(252, 56, 56, 0.2);
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.timeline ul li:nth-of-type(odd) > div {
  left: 45px;
}
.timeline ul li:nth-of-type(odd) > div>.san{

  height: 0;
  width: 0;
  border: 20px solid white;
  border-color: transparent white transparent transparent;

  // transform: rotate(45deg);
  position: absolute;
  left: -30px;
  bottom: -0%;
}
.timeline ul li:nth-of-type(even) > div {
  left: -240px;
}
.timeline ul li:nth-of-type(even) > div>.san{
  height: 0;
  width: 0;
  border: 20px solid white;
  border-color: transparent transparent transparent white;

  // transform: rotate(45deg);
  position: absolute;
  right: -30px;
  bottom: -0%;
}

.lineContent {
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}
.timeline ul li:nth-of-type(odd) div {
  opacity: 0;
}
.timeline ul li:nth-of-type(even) div {
  opacity: 0;
}
.timeline ul li.in-view div {
  transform: none;
  visibility: visible;
  opacity: 1;
}

@media screen and (max-width: 900px) {
  .lineContent {
    width: 200px;
    flex-direction: column;
  }
  .lineContent div {
    width: 80%;
    margin: 10px;
  }
  .timeline ul li:nth-of-type(even) > div {
    left: -239px;
  }
}
@media screen and (max-width: 600px) {
  body {
    background: #8bfff4;
  }

  .timeline ul li {
    margin-left: 20px;
  }
  .lineContent {
    width: calc(100vw - 120px);
    font-size: 30px;
  }
  .timeline ul li:nth-of-type(even) > div {
    left: 45px;
  }
}
// 产生滚动条的
@media screen and (max-width: 372px) {
  body {
    background: #8bfff4;
  }

  .timeline ul li {
    margin-left: 20px;
  }
  .lineContent {
    width:calc(100vw - 200px);
    font-size: 30px;
  }
  .timeline ul li:nth-of-type(even) > div {
    left: 45px;
  }
}